import { Injectable } from '@angular/core';
import { ToastController } from 'ionic-angular';
import { Http, Response, Headers, RequestOptions, URLSearchParams, RequestOptionsArgs } from '@angular/http';
import { Observable } from "rxjs";
import { Utils } from "./Utils";
import { Constants } from "./constants";

@Injectable()
export class HttpService {

  // toast: any = ToastController;

  constructor(
    public http: Http,
    private toastCtrl: ToastController) {
  }

  private extractData(res: Response) {
    let body = res.json();
    return body || {};
  }

  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const err = error || '';
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    //console.error(errMsg);
    return Observable.throw(errMsg);
  }

  public get(url: string, paramMap?: any): Observable<any> {
    return this.http.get(Constants.API_HOST + url, new RequestOptions({
      search: HttpService.buildURLSearchParams(paramMap)
      // headers: new Headers({
      //   'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
      // })
    })
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  // 默认Content-Type为application/json;
  public post(url: string, body: any = null, options?: RequestOptionsArgs): Observable<any> {
    return this.http.post(Constants.API_HOST + url, body, this.getOptions(options))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public postFormData(url: string, paramMap?: any): Observable<any> {
    let headers = new Headers({
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
      'Accept': 'application/json;charset=utf-8'
      // 'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
    });
    return this.http.post(Constants.API_HOST + url, HttpService.buildURLSearchParams(paramMap).toString(), new RequestOptions({ headers: headers }))
      .map(this.extractData)
      .catch(this.handleError);;
  }

  public put(url: string, body: any = null, options?: RequestOptionsArgs): Observable<any> {
    return this.http.put(url, body, this.getOptions(options))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public delete(url: string, paramMap?: any): Observable<any> {
    return this.http.delete(Constants.API_HOST + url, new RequestOptions({
      search: HttpService.buildURLSearchParams(paramMap)
      // headers: new Headers({
      //   'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
      // })
    }))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public patch(url: string, body: any = null, options?: RequestOptionsArgs): Observable<any> {
    return this.http.patch(Constants.API_HOST + url, body, this.getOptions(options))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public head(url: string, paramMap?: any): Observable<any> {
    return this.http.head(Constants.API_HOST + url, new RequestOptions({
      search: HttpService.buildURLSearchParams(paramMap)
      // headers: new Headers({
      //   'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
      // })
    }))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public options(url: string, paramMap?: any): Observable<any> {
    return this.http.options(Constants.API_HOST + url, new RequestOptions({
      search: HttpService.buildURLSearchParams(paramMap)
      // headers: new Headers({
      //   'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
      // })
    }))
      .map(this.extractData)
      .catch(this.handleError);
  }

  public static buildURLSearchParams(paramMap): URLSearchParams {
    let params = new URLSearchParams();
    if (!paramMap) {
      return params;
    }
    for (let key in paramMap) {
      let val = paramMap[key];
      if (val instanceof Date) {
        val = Utils.dateFormat(val, 'yyyy-MM-dd HH:mm:ss')
      }
      params.set(key, val);
    }
    return params;
  }

  public showMsg(msg, succ?: boolean): void {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 2000,
      position: 'top',
      cssClass: succ ? 'toast-custom-success' : 'toast-custom-warning'
    });
    toast.present();
  }

  private getOptions(options): RequestOptionsArgs {
    if (!options) {
      options = new RequestOptions({
        // headers: new Headers({
        //   'X-SESSION': this.storageService.get('lztoken')?this.storageService.get('lztoken'):'NOT_LOGIN_TOKEN'
        // })
      });
      return options;
    }
  }

}



